
body{
  background-color: #202124;
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: monospace;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #202124;
  -webkit-box-shadow:  0 2px 4px rgba(0,0,0,.5);
          box-shadow:  0 2px 4px rgba(0,0,0,.5);
  position: fixed;
  top: 0px;
  width: 100%;
  height: 100px;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  z-index: 10;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}
.App-main{
  position: relative;
  top:100px;
}
.Green{
  border-bottom:solid 3px #1e7d1e;
}
.Blue{
  border-bottom:solid 3px #3939f7;
}
.Red{
  border-bottom:solid 3px #f92121;
}
button{
  border: none;
}
.Button{
      outline: none;
      margin: 5px;
      width: 80px;
      height: 40px;
      border-radius: 3px;
      background-color: #464646a3;
      font-size: 17px;
      font-weight: 100;
      font-family: monospace;
      color: #b9b7b7;
      /*border-bottom: solid 3px;*/
}
.Button:active{
  background-color: #353535;
}
.Task{
  width: 80%;
  margin: 10px auto;
  padding: 16px;
  background-color: #6d6d6d;
  border-bottom: solid 3px #e4a20d;
  border-radius: 3px;
  color: #e4e4e4;
  font-size: 16px;
}


